header {
  position: fixed;
  width: 100%;
  z-index: 1;
  top: 0;
  background-color: #ffffffb8;
  backdrop-filter: blur(10px);
}

.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 21px 80px;
}

header img {
  width: 3rem;
}

.header-logo {
  display: flex;
  align-items: center;
  cursor: pointer;
  gap: 5px;
}

.logo {
  font-size: 22px;
  font-family: Poppins;
  font-weight: 700;
}

a {
  text-decoration: none;
  color: #343434;
}

nav > ul {
  display: flex;
  gap: 35px;
  align-items: center;
  padding-left: 0;
}

.nav-item {
  list-style: none;
  font-family: Poppins;
  opacity: 0.8;
}

.nav-item:hover {
  scale: 1.1;
  transition: all 200ms ease-in-out;
}

.nav-item .button {
  border: none;
  font-family: Poppins;
  color: #343434;
  font-size: 16px;
  padding: 0;
  cursor: pointer;
}

.nav-item a:hover,
.nav-item .button:hover {
  color: #2450ca;
}

.bag-logo .button {
  font-size: 28px;
  color: #0d5eba;
  position: relative;
}

.nav-login {
  display: flex;
  justify-content: flex-end;
}

.nav-login button {
  font-size: 16px;
  padding: 5px 10px;
  background-color: #fff;
  border-radius: 5px;
  border: 1px solid rgb(213, 213, 213);
  display: flex;
  align-items: center;
  gap: 10px;
  cursor: pointer;
  font-family: Poppins;
  opacity: 0.7;
}

.nav-login .button:hover {
  scale: 1.1;
  transition: all 200ms ease-in-out;
}

.cart-number {
  border: 1px solid rgb(255, 0, 0);
  border-radius: 13px;
  font-size: 12px;
  padding: 0 6px;
  background-color: rgb(255, 0, 0);
  color: #fff;
  text-align: center;
  position: absolute;
  top: 0;
  transform: translate(60%, 95%);
}


/* CartContainer */
.CartDisplay{
  position: fixed;
  display: flex;
  height: 100vh;
  flex-direction: column;
  width: 375px;
  right: 0;
  top: 0;
}
.CartHeader{
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  cursor: pointer;
  padding: 8px 16px;
}
.CartHeader p{
 font-size: 18px;
 line-height: 28px;
 font-weight: 600;
}
.CartHeaderel{
  display: flex;
  align-items: center;
  gap:8px;
  padding: 4px 8px;
  margin: 8px;
  border-radius: 6px;
  cursor: pointer;
  font-size: 16px;
  line-height: 24px;
}
/* Have cartItem */
.cartItemCon{
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
}
.cartItemCon .cartItemSec{
  width: 100%;
  height: 500px;
  display: flex;
  flex-direction: column;
  gap:12px;
  padding:40px 24px ;
}
.CartItem{
  width: 100%;
  padding: 4px 8px;
  border-radius: 8px;
  display: flex;
  gap: 8px;
  align-items: center;
}
.CartItem img{
  width: 80px;
  height: 80px;
  max-width: 240px;
  border-radius: 100%;
}
.CartItem .CartItemEle{
  display: flex;
  flex-direction: column;
  gap: 8px
}
.CartItem .CartItemEle2{
  display: flex;
  align-items: center;
  gap: 8px;
  cursor: pointer;
  margin-left: auto;
}
.CartItem .CartItemEle2 p{
  width: 20px;
  height: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 2px;
}

.CartTot{
  width: 100%;
  flex: 1 1 0%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  padding: 20px 32px;
  margin: 50px 0;
}
.CartTotEle{
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.CartTotEle p{
  font-size: 18px;
  line-height: 28px;
}



/* CartItem Empty*/
.cartIempty{
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 24px;
}
.cartIempty p{
  font-weight: 600;
  font-size: 20px;
  line-height: 28px;
}
